body{
  background-color: #85a19c;
  font: Arial;
  color: #515c5a;
}

.main{
 text-align: center;
}

.form-input{
 padding: 1%;
}

.form-input input{
  width: 30%;
  font-size: 15px;
  outline: none;
}

.form-input textarea{
  width: 55%;
  font-size: 15px;
  outline: none;
}

button{
  background-color: #b8d6d6;
  border: none;
  color: black;
  padding: 5px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  cursor: pointer;
  outline: none;
}
.blogs{
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}
.blog{
  border: 5px solid #d1d1d1;
  border-width: medium;
  margin-top: 3%;
  text-align: left;
  padding-left: 3%;
  margin-bottom: 2%
}
